import React, { useState } from "react";
import {
  motion,
  LayoutGroup,
  AnimatePresence,
  AnimateSharedLayout,
} from "framer-motion";
let data = [
  {
    id: 1,
    name: "praful",
    age: "11",
    gender: "male",
  },
  {
    id: 2,
    name: "keval",
    age: "41",
    gender: "male",
  },
  {
    id: 3,
    name: "yash",
    age: "23",
    gender: "male",
  },
  {
    id: 4,
    name: "rohit",
    age: "13",
    gender: "male",
  },
];

export default function Test() {
  const [state, setState] = useState();

  return (
    <AnimateSharedLayout type="crossfade">
      <div>
        <button
          onClick={() => {
            setState();
          }}
        >
          clear
        </button>
        {data.map((i) => (
          <Item data={i} setState={setState} />
        ))}

        <AnimatePresence>
          {state && (
            <motion.div
              layoutId={state.id}
              style={{
                position: "absolute",
                top: 10,
                left: 10,
                background: "red",
              }}
            >
              <motion.h1>{state.name}</motion.h1>
              <motion.h5>{state.age}</motion.h5>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </AnimateSharedLayout>
  );
}

let Item = ({ data, setState }) => {
  const ref = React.useRef(0);

  return (
    <motion.div
      layoutId={data.id}
      onClick={() => {
        setState(data);
      }}
    >
      <motion.h1>
        {data.name}
        {ref.current++}
      </motion.h1>
      <motion.h5>{data.age}</motion.h5>
    </motion.div>
  );
};
