import { useState, useEffect, useSyncExternalStore } from "react";

export function getStorageValue(key, defaultValue) {
  const saved = localStorage.getItem(key);
  const initial = JSON.parse(saved);
  return initial ?? defaultValue;
}

export const useStorage = (key, defaultValue, other = {}) => {
  const [value, setValue] = useState(() => {
    return typeof other.generateState === "function"
      ? other.generateState()
      : getStorageValue(key, defaultValue);
  });

  useEffect(() => {
    localStorage.setItem(key, JSON.stringify(value));
  }, [key, value]);

  return [value, setValue];
};

export const useSheetApi = (props) => {
  const { sheetName = "Sheet1", sheetId, columns } = props;

  const convertParams = (params = {}) => {
    let apiParams = [];
    Object.keys(columns).map((key, index) => {
      apiParams[index] = params[key];
    });

    return apiParams;
  };

  let parseRecords = (records) => {
    let finalData = [];
    records.map((record, mainIndex) => {
      Object.keys(columns || {}).map((key, index) => {
        if (!finalData[mainIndex]) {
          finalData[mainIndex] = {};
        }
        finalData[mainIndex][key] = record[index];
      });
    });
    return finalData;
  };

  const createRecordAPI = (params) => {
    let apiParams = convertParams(params);
    console.log("apiParams", apiParams);
  };

  const getListingAPI = (props) => {
    const { offset = 0, limit = 16 } = props || {};

    return new Promise((res, rej) => {
      window.gapi.client.sheets.spreadsheets.values
        .get({
          spreadsheetId: sheetId,
          range: `${sheetName}!${offset || 1}:${offset + limit}`,
          majorDimension: "ROWS",
        })
        .then((response) => {
          let finalData = parseRecords(response?.result?.values || []);
          console.log("finalData", finalData);
          res(finalData);
        })
        .catch((error) => {
          rej(error);
        });
    });
  };

  const getByIdAPI = (offset) => {
    // gapi.client.sheets.spreadsheets.values
    //   .get({
    //     spreadsheetId: sheetId,
    //     range: `${sheetName}!${offset}:1`,
    //     majorDimension: "ROWS",
    //   })
    new Promise((res, rej) => {
      getListingAPI({ offset: offset, limit: 1 })
        .then((response) => {
          let finalData = parseRecords(response?.result?.values || []);
          res(finalData[0]);
        })
        .catch((error) => {
          rej(error);
        });
    });
  };

  return { getListingAPI, getByIdAPI };
};

export const useApiCall = (apiCallback) => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState();
  const [error, setError] = useState();

  const callApi = async (...all) => {
    setLoading(true);
    if (!apiCallback) return false;
    apiCallback(...all)
      .then((res) => {
        setLoading(false);
        setData(res);
      })
      .catch((error) => {
        setLoading(false);
        setError(error);
      });
  };

  return { callApi, loading, data, error };
};
